import React, { useEffect, useRef, useState } from "react";

// react-bootstrap components
import {
  Badge,
  Button,
  Card,
  Form,
  Navbar,
  Nav,
  Container,
  Row,
  Col,
} from "react-bootstrap";

function User() {
    const inputProjectName = useRef(); 
    const inputDuration = useRef();
    const inputClient = useRef();
    const inputAmount = useRef();
    const inputDescription = useRef();
    const [message, setMessage] = useState('');
    const submitProjectDetails = () => {
        let projectDetails = {
            projectName: inputProjectName.current.value,
            clientName: inputClient.current.value,
            projectDescription: inputDescription.current.value,
            duration: inputDuration.current.value,
            amount: inputAmount.current.value
        };
        if (inputProjectName.current.value !== '' && inputClient.current.value !== '' && inputDescription.current.value !== '' && inputDuration.current.value !== '' && inputAmount.current.value) {
            fetch("https://localhost:44377/ProjectDetails/", {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(projectDetails)
            }).then(r => r.json()).then(res => {
                if (res) {
                    setMessage("Project Details Inserted Successfully!");
                }
            })
        }
        else {
            setMessage("Please fill all the input fields");
        }
        //console.log(inputProjectName.current.value + " " + inputDuration.current.value + " " + inputDescription.current.value);
    }  
  return (
    <>
      <Container fluid>
        <Row>
          <Col md="8">
            <Card>
              <Card.Header>
                <Card.Title as="h4">Add Project Details</Card.Title>
              </Card.Header>
              <Card.Body>
                <Form>
                  <Row>
                    <Col className="pr-1" md="8">
                      <Form.Group>
                        <label>Project Name</label>
                        <Form.Control
                         defaultValue=""
                         placeholder="Project Name"
                         type="text"
                         ref={inputProjectName}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                    <Col className="px-1" md="4">
                      <Form.Group>
                        <label>Duration (in months)</label>
                        <Form.Control
                          defaultValue=""
                          placeholder="Duration (in months)"
                          type="text"
                          ref={inputDuration}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                  </Row>
                  <Row>
                    <Col className="pr-1" md="6">
                      <Form.Group>
                        <label>Client</label>
                        <Form.Control
                          defaultValue=""
                          placeholder="Client Name"
                          type="text"
                          ref={inputClient}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                    <Col className="pl-1" md="6">
                      <Form.Group>
                        <label>Amount</label>
                        <Form.Control
                          defaultValue=""
                          placeholder="Amount (per month)"
                          type="text"
                          ref={inputAmount}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                  </Row>
                  <Row>
                    <Col md="12">
                      <Form.Group>
                        <label>Brief Description about Project</label>
                        <Form.Control
                          cols="80"
                          defaultValue=""
                          placeholder="Here can be your description"
                          rows="4"
                          as="textarea"
                          ref={inputDescription}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                  </Row>
                  <Button
                      className="btn-fill pull-right"
                      type="button"
                      variant="info"
                      onClick={submitProjectDetails}
                  >
                      Submit
                  </Button><br/>
                  <div><label style={{ color: 'red', fontWeight:'bold' }}>{message}</label></div>  
                  <div className="clearfix"></div>
                </Form>
              </Card.Body>
            </Card>
          </Col>
          <Col md="4">
            <Card className="card-user">
              <div className="card-image">
                <img
                  alt="..."
                  src={
                    require("assets/img/photo-1431578500526-4d9613015464.jpeg")
                      .default
                  }
                ></img>
              </div>
              <Card.Body>
                <div className="author">
                  <a href="#pablo" onClick={(e) => e.preventDefault()}>
                    <img
                      alt="..."
                      className="avatar border-gray"
                      src={require("assets/img/faces/Photo.jpg").default}
                    ></img>
                    <h5 className="title">P Naga Venkata Raj</h5>
                  </a>
                  <p className="description">Freelancer</p>
                </div>
                <p className="description text-center">
                  "14+ years experienced in Microsoft Technologies"
                </p>
              </Card.Body>
              
            </Card>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default User;
