import React, { useEffect, useState } from "react";

// react-bootstrap components
import {
  Badge,
  Button,
  Card,
  Navbar,
  Nav,
  Table,
  Container,
  Row,
  Col,
} from "react-bootstrap";

function TableList() {
    const [projectDetails, setProjectDetails] = useState([]);
    useEffect( () => {
        fetch("https://localhost:44377/ProjectDetails/", {
            method: 'GET',
        }).then(r => r.json()).then(res => {
            setProjectDetails(res);
        })
    }, [])
  console.log(projectDetails);
  return (
    <>
      <Container fluid>
        <Row>
          <Col md="12">
            <Card className="strpied-tabled-with-hover">
              <Card.Header>
                <Card.Title as="h4">Project(s) Worked</Card.Title>
              </Card.Header>
              <Card.Body className="table-full-width table-responsive px-0">
                <Table className="table-hover table-striped">
                  <thead>
                    <tr>
                      <th className="border-1">Project Name</th>
                      <th className="border-1">Client Name</th>
                      <th className="border-0">Project Description</th>
                      <th className="border-0">Duration (in months)</th>
                      <th className="border-0">Amount (per month)</th>
                    </tr>
                  </thead>
                  <tbody>
                      {
                        projectDetails.map(project =>
                            <tr>
                                <td>{project.projectName}</td>
                                <td>{project.clientName}</td>
                                <td>{project.projectDescription}</td>
                                <td>{project.duration}</td>
                                <td>{project.amount}</td>
                            </tr>
                      )}
                  </tbody>
                </Table>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default TableList;
